import { emojiList } from "@/common/constants";
/**
 * emoji emoji表情组件
 * @description emoji表情组件
 * @Author: 穆兰
 * @Date: 2022/1/7
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/14
 * @event {Function} setEmoj 点击某个表情图标
 * @example 调用示例
 */
export default {
    props: {
        contentHeight: {
            type: Number,
            default: 300
        }
    },
    data() {
        return {
            emojiList
        };
    },
    methods: {
        setEmoj(item, index) {
            this.$nextTick(() => {
                this.$emit('setEmoj', item, index);
            });
        }
    }
};
